const rules={

	common:{
		id(len_min,len_max,text){
			return [
	    		{ required: true, message: text, trigger: 'blur' },
	        	{ min: len_min, max: len_max, message: ` ${len_min}${len_max}`, trigger: 'blur' }
	    	]
		},
	},

	user:{
		
        //
		username(len_min,len_max,text){
			return [
	    		{ required: true, message: text, trigger: 'blur' },
	        	{
	        		validator:(rule,value,callback) =>{
	        			if(value.match(/^[a-zA-Z0-9]{5,16}$/)){
	        				callback();
	        			}else{
	        				callback("5-16")
	        			}
	        		},
	        		trigger:'blur'
	        	}
	    	]
		},
        
        //
		password(len_min,len_max,text) {
			 return [
		        { required: true, message: text, trigger: 'blur' },
		        { validator:(rule,value,callback) =>{
	        			if(value.match(/^[a-zA-Z0-9]{5,16}$/)){
	        				callback();
	        			}else{
	        				callback("6-16")
	        			}
	        		},
	        		trigger:'blur' 
		        }
			]
		},
        
        //
		phone(){
			return [
	        		{ required: true, message: '', trigger: 'blur' },
	          		{
	                	validator: (rule, value, callback) => {
		                    if (value.match(/^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/)){
		                        callback();
		                    } else {
		                        callback("");
		                    }
	                	},
	                	trigger: 'blur'
	            	}
	        	]
		},        
        
        //
		truename(){
			return [
	    		{ required: true, message: '', trigger: 'blur' },
	    		
	        	{
	                validator: (rule, value, callback) => {
		                    if (value.match(/^[a-zA-Z\u4e00-\u9fa5]{2,8}$/)){
		                        callback();
		                    } else {
		                        callback("2-8");
		                    }
	                	},
	                	trigger: 'blur'
	            	}
	    	]
		},
		
		//
		gender(){
			return [
			    { required: true, message: '', trigger: 'blur' }
			]
		}
	},
	
	
}

export default {
  rules
}


